export default {
	data() {
		return {
			slot: "顶吸",
			money: "2.00",
			num1: "2",
			slot1: "22",
			service: false, //服务订单
			shop: true, //商品订单
			obligation: true, //付款
			accomplish: true, //完成
			evaluate: true, //评价
			waitservice: true, //待服务
			ml: 300,
			shopmoney: 3000,
			specification: 6000,
			bg: "bg1",
			type:0,
			list:0,
			tabs:[
				{name:"全部",check:true},
				{name:"待付款",check:false},
				{name:"待发货",check:false},
				{name:"待收货",check:false},
				{name:"已完成",check:false},
			],
			Tabs:[
				{name:"全部",check:true},
				{name:"待付款",check:false},
				{name:"待服务",check:false},
				{name:"待评价",check:false},
				{name:"已完成",check:false},
			],
			Buttons:[
				{name:"服务订单",check:true},
				{name:"商城订单",check:false}
			]
		}
	},
	methods: {
		ItemButtonChange(index){
			this.list = index
		},
		tabChange(i){
			this.type=i;
		},
		ItemChange(index){
			this.type=index;
		},
		appraise() {
			uni.navigateTo({
				url: "../../components/appraise/appraise"
			})
		},
		Toshop() {
			this.service = false
			this.shop = true
		},
		Toservice() {
			this.service = true
			this.shop = false
		},
		all() {
			this.obligation = true; //服务订单
			this.accomplish = true; //商品订单
			this.evaluate = true; //付款
			this.waitservice = true; //评价
			this.bg = "bg2"
		},
		Obligation() {
			this.obligation = true, //服务订单
				this.accomplish = false, //商品订单
				this.evaluate = false, //付款
				this.waitservice = false //评价
		},
		WaiteValuate() {
			this.obligation = false, //服务订单
				this.accomplish = false, //商品订单
				this.evaluate = false, //付款
				this.waitservice = true //评价
		},
		Over() {
			this.obligation = false, //服务订单
				this.accomplish = true, //商品订单
				this.evaluate = false, //付款
				this.waitservice = true //评价
		}
	}
}
